@import url(https://fonts.googleapis.com/css2?family=Open+Sans&display=swap);
*{box-sizing:border-box;margin:0;padding:0}body{font-family:"Open Sans", Verdana, sans-serif;color:#fcfdf4}button{padding:5px;color:#fcfdf4;background-color:#5eae7c;border:1px solid #fcfdf4;border-radius:5px;cursor:pointer}button:hover{background-color:#a8a80f;transition:0.4s}button:disabled{background-color:#c0c0c0;color:#000;border:1px solid #000;cursor:default}h3{text-align:center;margin-bottom:10px}.container{height:100vh}.header{display:grid;justify-content:center;min-height:10vh;padding:5px;background-color:#72b3d1}.header__game-title{text-align:center;font-size:1.2rem}.header__buttons{display:flex;justify-content:space-around;grid-gap:5px;gap:5px}.main{position:relative;display:flex;flex-direction:column;justify-content:center;align-items:center;min-height:80vh;padding:10px;background-color:#cb89bc}.message{margin-bottom:20px}.game-container{display:grid;grid-gap:20px;grid-template-columns:repeat(auto-fit, minmax(300px, 1fr));width:100%}.game-container__game-field{justify-self:end}.game-board{display:grid;justify-content:center}.game-board-small{grid-template-columns:repeat(3, 100px);grid-template-rows:repeat(3, 100px)}.game-board-medium{grid-template-columns:repeat(5, 60px);grid-template-rows:repeat(5, 60px)}.game-board-large{grid-template-columns:repeat(7, 45px);grid-template-rows:repeat(7, 45px)}.letter{text-align:center;color:#fcfdf4;font-size:1.4rem;border:1px solid #47483c;background-color:#5eae7c}.letter--show{cursor:pointer}.letter--highlighted{background-color:#a8a80f}@keyframes success{from{background-color:#ffff80}to{background-color:#5eae7c}}.letter--success{animation:success 0.4s 3}@keyframes failure{from{background-color:red}to{background-color:#5eae7c}}.letter--failure{animation:failure 0.4s}.buttons-container{display:grid;grid-template-columns:repeat(auto-fit, minmax(80px, 1fr));grid-gap:10px;background-color:#cb89bc;margin-bottom:5px}.fullscreen-button{display:grid;justify-content:center;align-content:center;padding:0;background-color:transparent;border:none;width:30px;height:30px}.fullscreen-button:hover{background-color:transparent}.fullscreen-button svg{width:30px;height:30px;fill:#fcfdf4;transition:0.4s}.fullscreen-button svg:hover{width:36px;height:36px}.score-container{justify-self:start;display:flex;background-color:#a25fd8;padding:10px;min-width:200px}.player-score{word-break:break-all}.player-score:not(:last-child){max-width:50%;margin-right:10px}.player-score__header--moving{color:#ffff80}@keyframes winner{from{color:#fcfdf4}to{color:#ff8040}}.player-score__header--winner{animation:winner 0.4s 3 forwards}.player-score__pair{display:flex;justify-content:flex-start}.player-score__word{margin-right:5px}.player-score__total{font-size:1.4rem;color:#f7ac68}.new-game-form{padding:20px;text-align:center}.new-game-form fieldset{padding:10px;border:5px solid #a25fd8}.new-game-form__players{display:grid;grid-gap:10px}.new-game-form__players input{height:30px;padding:5px;font-size:1.2rem}.records,.settings{z-index:10;position:absolute;top:50%;left:50%;min-height:200px;padding:20px;transform:translate(-50%, -50%);display:grid;justify-content:center;align-content:center;background-color:#72b3d1}.settings__form{display:grid;grid-gap:10px}.settings__form fieldset{padding:10px}.settings__checkbox-pair{display:grid;grid-template-columns:repeat(auto-fit, minmax(40px, 1fr));align-items:center;margin-bottom:5px}.records__table{min-width:200px;margin-bottom:20px}.records__table th{text-align:left;color:#ffac84}.records__table td{text-align:left}.records__no-records{margin-bottom:20px}.records__buttons{display:flex;justify-content:space-around}.footer{display:grid;grid-gap:20px;grid-template-columns:repeat(3, auto);justify-content:center;justify-items:center;align-items:center;min-height:10vh;padding:10px;background-color:#9fa35f}.footer__gh-link{color:#000}.footer__logo{width:100px}@media (max-width: 820px){html{font-size:12px}.game-container__game-field{justify-self:center}.score-container{justify-self:center}}

